<template>
  <div class="pdf-demo">
    <el-button class="pdf-print-btn" type="primary" plain @click="goPrint" size="small">打印</el-button>
    <!-- 根据pdf生成canvas -->
    <div class="pdf-list" id="pdf-list">
      <div style="page-break-after: always;" v-for="(item, index) in list" :key="index">
        <h1>第{{index+1}}份pdf</h1>
        <!-- 这是不分页预览 -->
        <div class="pdf-sub-list" v-for="(item2, index2) in list[index].pageCount" :key="index2">
          <div>第{{index2+1}}页</div>
          <pdf :src="list[index].src" :page="index2+1"></pdf>
        </div>
        <pdf :src="list[index].src" @num-pages="list[index].pageCount=$event" style="display: none;"></pdf>
      </div>
    </div>
    <!-- 根据canvas生成img，=》然后打印 -->
    <div class="print-list-container" style="display: none;">
      <div class="page-list print-list" ref="print">
        <div class="page" style="page-break-after: always;position: relative; text-align: center;" v-for="(item, index) in img_urlList" :key="index">
          <img :src="item" alt="" style=""></img>
          <div v-if="options.title.checked" :style="options.title.style" class="page-info">{{options.title.value}}</div>
          <div v-if="options.date.checked" :style="options.date.style" class="page-info">{{options.date.value}}</div>
          <div v-if="options.pageNo.checked" :style="options.pageNo.style" class="page-info">{{index+1}}/{{img_urlList.length}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import pdf from 'vue-pdf'
  import { mapGetters } from 'vuex'
  export default {
    components: {
      pdf,
    },
    data() {
      return {
        list: [],
        img_urlList: []
      }
    },
    computed: {
      ...mapGetters(['options'])
    },
    created() {},
    mounted() {
      this.list = [{ src: './static/pdfs/test.pdf', pageCount: 1 }, { src: './static/pdfs/test.pdf', pageCount: 1 }]
    },
    methods: {
      goPrint() {
        console.log(this.options)
        let canvas = document.querySelectorAll('#pdf-list .pdf-sub-list canvas')
        console.log(canvas)
        for (let i = 0; i < canvas.length; i++) {
          this.img_urlList[i] = canvas[i].toDataURL("image/png")
        }
        this.img_urlList = [...this.img_urlList]
        console.log(this.img_urlList)
        this.$nextTick(() => {
          this.$print(this.$refs.print, {
            'title': 'this is a test title'
          })
        })
      },
    }
  }
</script>

<style scoped>
  .pdf-demo {
    margin: 0 auto;
    padding: 50px 20px;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: hidden;
  }

  .page-list {
    overflow-x: hidden;
    overflow-y: auto;
  }

  .print-list-container,
  .page-list,
  .page {
    width: 100%;
    height: 100%;
  }

  .pdf-print-btn {
    position: fixed;
    right: 0;
    top: 50%;
    z-index: 100;
  }
</style>



